import { LAYER0_DEPLOYMENT_TYPE_AWS, LAYER0_ENV_VARIABLES } from './constants'

/**
 * Used to determine if the app is running in the cloud.
 * @returns `true` when running in the cloud, `false` otherwise
 */
export function isCloud(): boolean {
  return process.env[LAYER0_ENV_VARIABLES.deploymentType] === LAYER0_DEPLOYMENT_TYPE_AWS
}

/**
 * Returns true when running locally, either in dev or production mode
 */
export function isLocal(): boolean {
  return process.env.LAYER0_LOCAL === 'true'
}

/**
 * Used to determine if the app is running on a production build.
 * @returns `true` when running a production build, either locally or in the cloud; `false` otherwise
 */
export function isProductionBuild(): boolean {
  return process.env.NODE_ENV === 'production'
}

/**
 * Used to determine if the app is running in `layer0 run` development environment
 * @returns `true` when running with `layer0 run`without production flag, `false` otherwise
 */
export function isLayer0RunDev(): boolean {
  return !isCloud() && !isProductionBuild()
}
