import get from './utils/get'
import has from './utils/has'
import merge from 'deepmerge'
import { existsSync } from 'fs'
import { join } from 'path'
import { LAYER0_ENV_VARIABLES, LAYER0_CONFIG_FILE } from './constants'
import Validator from './utils/Validator'
import nonWebpackRequire from './utils/nonWebpackRequire'

/**
 * An object containing the config settings. This class is internal, but an instance
 * can be obtained using.
 *
 * ```js
 * import config from '@layer0/core/config'
 * ```
 *
 * @internal
 */
class Config {
  private values?: any

  readonly defaults = {
    production: true,
    backends: {},
    includeNodeModules: false,
    routes:
      ['routes.js', 'routes.ts', 'routes.cjs'].find(file =>
        existsSync(join(process.cwd(), file))
      ) || 'routes.js',
  }

  /**
   * Get configuration by key. Will throw an error if no value is configured
   * @param key The key to grab from the config.
   * @param fallback A value to return if a value is not configured
   */
  get(key: string, fallback?: any): any {
    if (this.has(key)) {
      return get(this.values, key)
    } else if (typeof fallback !== 'undefined') {
      return fallback
    } else {
      throw new Error(`Layer0 configuration key: "${key}" is not defined`)
    }
  }

  /**
   * Returns true if a value has been configured for a specific key, otherwise false.
   * @param key The key to test.
   */
  has(key: string): boolean {
    if (!this.values) this.loadLayer0Config()
    return has(this.values, key)
  }

  /**
   * Returns all config values
   */
  get all() {
    if (!this.values) this.loadLayer0Config()
    return this.values
  }

  /**
   * Loads Layer0 config and sets the current configuration.
   */
  private loadLayer0Config() {
    const file = this.loadFromFile()
    const env = this.loadFromEnv()
    this.values = merge.all([{}, this.defaults, file, env])
  }

  /**
   * Load configuration from file
   * @private
   */
  private loadFromFile() {
    const configAbsPath = [
      join(process.cwd(), LAYER0_CONFIG_FILE),
      join(process.cwd(), LAYER0_CONFIG_FILE.replace('.js', '.cjs')),
    ].find(existsSync)

    if (configAbsPath) {
      try {
        const fileConfig = nonWebpackRequire(configAbsPath)
        return Validator.validateLayer0FileConfiguration(fileConfig)
      } catch (e) {
        throw new Error(`Failed to load ${configAbsPath}: ${e.message}`)
      }
    }

    return {}
  }

  /**
   * Load configuration from env var
   * @private
   */
  private loadFromEnv() {
    if (process.env[LAYER0_ENV_VARIABLES.config]) {
      try {
        return JSON.parse(<string>process.env[LAYER0_ENV_VARIABLES.config])
      } catch (e) {
        throw new Error(
          `Layer0 configuration variable: ${LAYER0_ENV_VARIABLES.config}, is not parsable as json`
        )
      }
    }

    return {}
  }
}

/**
 * Create a Config instance.
 */
export default new Config()
