/**
 * `get` function instead of importing lodash.
 *
 * Note that this function is not entirely equivalent to lodash's but it uses far less space in the bundle.
 */
export default function get(object: any, path: string): any {
  let value = object
  const components = path.split('.')
  for (let component of components) {
    if (typeof value === 'undefined') {
      return undefined
    }
    value = value[component]
  }
  return value
}
